<template>
  <div>
    <div class="clearfix top-filter">
      <el-form ref="filterItem" :model="query" inline @submit.native.prevent>
        <el-form-item prop="nameOrCode">
          <el-input v-model="query.nameOrCode" filterable clearable placeholder="物料编号或名称" style="width: 178px;" class="filter-item" @change="handleSearch" />
        </el-form-item>
        <el-button
          class="filter-item"
          type="success"
          icon="el-icon-search"
          @click="handleSearch"
        >搜索</el-button>
        <el-button class="filter-item fr" @click="handleGoBack">返回</el-button>
      </el-form>
    </div>
    <el-row />
    <el-row>
      <el-descriptions :column="3" :label-style="{fontWeight: 900}" :content-style="{paddingRight: '10px',alignItems: 'center'}">
        <el-descriptions-item label="交接单号">{{ baseInfo.no || '---' }}</el-descriptions-item>
        <el-descriptions-item label="检验单号">{{ baseInfo.inspectNo || '---' }}</el-descriptions-item>
        <el-descriptions-item label="物料项数">{{ baseInfo.materialCount || '---' }}</el-descriptions-item>
        <el-descriptions-item label="物料总数">{{ baseInfo.materialTotal || '---' }}</el-descriptions-item>
        <el-descriptions-item label="创建时间">{{ baseInfo.createDate || '---' }}</el-descriptions-item>
        <el-descriptions-item label="接收人">{{ baseInfo.recipientName || '---' }}</el-descriptions-item>
      </el-descriptions>
    </el-row>
    <tabs v-if="tabsList.length" ref="Tabs" :data="tabsList" height="650px" @tabPanelClick="handleTabClick">
      <template #main-container="{handleClick}">
        <right
          ref="rightView"
          :key="rightKey"
          :node="nodeInfo"
          @enterNextMaterial="enterNextMaterial(handleClick)"
        />
      </template>
    </tabs>
  </div>
</template>

<script>
import Right from './Right'
import Tabs from './Tabs'
import { noCacheTreeMixins } from './tree.mixin'
import { getDetailList1Api as getListApi, getInspectNoDetailApi } from '@/apis/modules/in-storage/inspection'

export default {
  name: 'InStorageInspectionStart',
  components: {
    Right,
    Tabs
  },
  filters: {
    ellipsis(value, length) {
      if (value && value.length > length) {
        return value.slice(0, length) + '...'
      } else {
        return value
      }
    }
  },
  mixins: [noCacheTreeMixins],
  data() {
    return {
      query: {
        nameOrCode: ''
      },
      nodeInfo: {},
      nodeIndex: 0,
      rightKey: 1,
      baseInfo: {},
      activeName: 'first',
      tabsList: []
    }
  },
  created() {
    this.getInspectNoDetail()
      .then(res => {
        this.init()
      })
  },
  methods: {
    init() {
      return new Promise((resolve, reject) => {
        getListApi({ deliveryReceiptId: this.$route.params.id }).then(res => {
          const { code, data } = res
          if (!code) {
            this.tabsList = data
          }
          resolve(res)
        }).catch(err => {
          reject(err)
        })
      })
    },
    handleClick(tab, event) {
      console.log(tab, event)
    },
    // 进入下一个物料
    enterNextMaterial(callback) {
      if (this.tabsList.length) {
        if (this.tabsList.length === 1) return // 如果只有一个物料，则不会往下执行
        if (this.tabsList.length > 1) {
          this.nodeIndex++
          if (this.nodeIndex > this.tabsList.length - 1) { // 如当前为最后一个物料，则切换到第一个物料
            this.nodeIndex = 0
          }
          callback(this.tabsList[this.nodeIndex], this.nodeIndex)
        }
      }
    },
    // 搜索
    handleSearch() {
      this.$nextTick(() => {
        if (this.query.nameOrCode) {
          this.tabsList.forEach((c, i) => {
            if (c.materialCode === this.query.nameOrCode || c.materialName === this.query.nameOrCode) {
              this.$refs.Tabs.handleClick(c, i)
            }
          })
          return
        }
        // this.$refs.Tabs.handleClick(this.tabsList[0], 0)
      })
    },
    // 返回
    handleGoBack() {
      this.$store.dispatch('tagsView/delView', this.$route)
      this.$router.push({
        name: 'InStorageInspectionDetail',
        params: {
          id: this.$route.params.id
        }
      })
    },
    // 获取检验单详情
    getInspectNoDetail() {
      return new Promise((resolve, reject) => {
        getInspectNoDetailApi({ id: this.$route.params.id })
          .then(res => {
            const { code, data } = res
            if (!code) {
              this.baseInfo = data
            }
            resolve(res)
          })
          .catch(err => {
            reject(err)
          })
      })
    },
    handleTabClick({ item, index }) {
      // this.query.nameOrCode = item.materialCode
      this.rightKey++
      this.nodeInfo = { ...item }
      this.nodeIndex = index
    }
  }
}
</script>

<style scoped lang="scss">
.custom-tree-node {
  width: 100%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
</style>
