<template>
  <div class="app-container">
    <!-- 已完成或者已入库 保存按钮禁用 -->
    <div :class="{'sticky-header': isStickyBtn, 'absolute-header': !isStickyBtn}">
      <el-button class="save-btn" type="primary" :disabled="[3, 4].includes(baseInfo.status)" @click="handleSave">保存</el-button>
    </div>
    <!-- 上半部分 -->
    <el-card class="box-card-rt" shadow="never" style="border: none;">
      <div slot="header" class="clearfix">
        <div class="clearfix" style="display: flex;align-items: center;">
          <div>
            <span style="display: block;padding: 0 15px;">物料信息</span>
          </div>
        </div>
      </div>
      <el-descriptions :column="3" :label-style="{fontWeight: 900}" :content-style="{paddingRight: '10px',alignItems: 'center'}">
        <el-descriptions-item label="物料编号">{{ baseInfo.materialCode || node.materialCode || '---' }}</el-descriptions-item>
        <el-descriptions-item label="物料名称">{{ baseInfo.materialName || node.materialName || '---' }}</el-descriptions-item>
        <el-descriptions-item label="到货数量">{{ baseInfo.deliveryNumber || node.deliveryNumber || '---' }}</el-descriptions-item>
      </el-descriptions>
      <el-form ref="form" :model="form" :rules="rules" inline :disabled="[3, 4].includes(baseInfo.status)">
        <el-form-item prop="inspectType" label="检验类型" style="margin: 0;">
          <el-radio-group v-model="form.inspectType" class="filter-item" style="width: 178px;">
            <el-radio-button v-for="(item, index) in [{name: '全检', value: 1}, {name: '抽检', value: 2}]" :key="index" :label="item.value">{{ item.name }}</el-radio-button>
          </el-radio-group>
        </el-form-item>
      </el-form>
    </el-card>
    <!-- 中间部分(不按最小单位SKU打码展示包装条码列表) -->
    <el-card v-if="!baseInfo.useMinUnit" class="box-card-rb" shadow="never">
      <div slot="header" class="clearfix">
        <div class="clearfix" style="display: flex;align-items: center;">
          <div>
            <span style="display: block;padding: 0 15px;">包装条码列表</span>
          </div>
        </div>
      </div>
      <table-com
        :key="refreshKey"
        ref="dmdTableRef"
        :loading="dmdLoading"
        :index="true"
        :selection="false"
        :table-data="packageCodeList"
        :column-list="dmdColumnList"
        :table-height-type="340"
        :operation-width="240"
        :table-column-sort="false"
        :show-operate="true"
        @rowClick="handleRowClick1"
      >
        <template slot-scope="scope">
          <el-button type="warning" :disabled="[3, 4].includes(baseInfo.status)" @click="handleCaptchaHumanBypass(scope.row)">不合格品打码</el-button>
          <el-button type="primary" @click="handlePrintBarcode(scope.row)">打印条码</el-button>
        </template>
      </table-com>
    </el-card>
    <!-- 下半部分(按最小单位SKU打码展示不合格条码列表) -->
    <el-card class="box-card-rb" shadow="never">
      <div slot="header" class="clearfix">
        <div class="clearfix" style="display: flex;align-items: center;">
          <div>
            <span style="display: block;padding: 0 15px;">不合格条码列表</span>
          </div>
          <div style="flex: 1;">
            <div class="clearfix top-filter">
              <el-button v-if="baseInfo.useMinUnit" class="fr" type="danger" style="margin-right: 20px;" :disabled="[3, 4].includes(baseInfo.status)" @click="handleScanQRCodes">不合格品扫码</el-button>
            </div>
          </div>
        </div>
      </div>
      <el-row>
        <table-com
          ref="baseTableRef"
          :key="refreshKey"
          :index="true"
          :selection="false"
          :table-height-type="420"
          :table-data="tableData"
          :column-list="columnList"
          :operation-width="240"
          :show-operate="true"
          :table-column-sort="false"
          @rowClick="handleRowClick2"
        >
          <template slot-scope="scope">
            <el-button type="primary" @click="handlePrintBarcode(scope.row)">打印条码</el-button>
            <el-button type="danger" :disabled="[3, 4].includes(baseInfo.status)" @click="handleDeleteBarcode(scope.row, scope.$index)">删除</el-button>
          </template>
        </table-com>
      </el-row>
    </el-card>
    <captcha-human-bypass v-model="visible" :bar-code-list="tableData" :bar-code-number="currentItem.num" :extra-info="{deliveryMaterialId: /** 交接物料ID */ node.id, deliveryMaterialDetailId: /** 交接物料详情ID */ currentItem.deliveryMaterialDetailId, isShowHistoryUnqualified }" @confirm="handleCaptchaHumanBypassConfirm" />
    <scan-qr-codes v-model="visible1" :bar-code-list="tableData" :extra-info="{deliveryMaterialId: /** 交接物料ID */ node.id}" @confirm="handleScanConfirm" />
  </div>
</template>

<script>
// eslint-disable-next-line no-unused-vars
import _ from 'lodash'
import paginationPage from '@/mixins/paginationPage'
import printTagWired from '@/mixins/printTagWired'
// eslint-disable-next-line no-unused-vars
import { saveInspectResultApi as saveApi, getBarCodeListByInspectTypeApi as getBarCodeListApi, getInspectResultByDeliveryMaterialIdApi as getDetailApi, barcodePrintApi, packageCodeSamplingListApi, packageCodeSamplingSplitApi, getGenerateBarcodeListApi, deleteUnqualifiedBarcodeApi, getUnqualifiedBarcodeListApi, editUnqualifiedBarcodeReasonApi } from '@/apis/modules/in-storage/inspection'
import ScanQrCodes from './ScanQRCodes'
import CaptchaHumanBypass from './CaptchaHumanBypass'
// import { _debounce } from '@/utils'
const INIT_FORM = () => ({
  materialCode: '', // 物料编号
  materialName: '', // 物料名称
  deliveryNumber: '', // 到货数量
  dmiList: [] // 条码列表
})

export default {
  name: 'InStorageInspectionDetail',
  components: {
    ScanQrCodes,
    CaptchaHumanBypass
  },
  mixins: [paginationPage, printTagWired],
  props: {
    node: {
      type: Object,
      default: () => ({})
    }
  },
  data() {
    return {
      title: this.$route.meta.title,
      form: {
        inspectType: ''
      },
      rules: {
        inspectType: [
          { required: true, message: '请选择检验类型', trigger: 'change' }
        ]
      },
      baseInfo: INIT_FORM(),
      refreshKey: new Date().getTime(),
      /** 不合格品打码相关 */
      visible: false,
      /** 包装码列表相关 */
      dmdColumnList: [
        {
          module: 'word',
          prop: 'barCode',
          label: '条码号',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'number',
          label: '数量',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'unitName',
          label: '计量单位',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        }
      ],
      dmdLoading: false,
      packageCodeList: [],
      currentItem: {},
      /** 不合格品扫码相关 */
      visible1: false,
      /** 不合格品条码列表相关 */
      columnList: [
        {
          module: 'word',
          prop: 'barCode',
          label: '条码号',
          align: 'center',
          minWidth: '200',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'snNo',
          label: '序列号',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'number',
          label: '数量',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'inspectResult',
          label: '检验结论',
          align: 'center',
          minWidth: '200',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: (row) => row.inspectResult ? '合格' : '不合格',
          headerAlign: 'center'
        },
        // {
        //   module: 'select',
        //   prop: 'inspectResult',
        //   label: '检验结论',
        //   align: 'center',
        //   minWidth: '',
        //   fixed: false,
        //   sortable: false,
        //   resizable: true,
        //   formatter: null,
        //   headerAlign: 'center',
        //   placeholder: '请选择',
        //   optionLabel: 'name', // 下拉选框绑定的 label 字段的key
        //   optionValue: 'id', // 下拉选框绑定的 value 字段的key
        //   setDisabled: (row) => {
        //     return [3, 4].includes(this.baseInfo.status)
        //   },
        //   getOptions: (item) => {
        //     return [{ name: '不合格', id: false }, { name: '合格', id: true }]
        //   }
        // },
        // {
        //   module: 'input',
        //   prop: 'remarks',
        //   label: '不合格原因',
        //   align: 'center',
        //   width: 150,
        //   minWidth: '',
        //   fixed: false,
        //   sortable: false,
        //   resizable: true,
        //   formatter: null,
        //   headerAlign: 'center',
        //   disabled: (item) => [3, 4].includes(this.baseInfo.status)
        //   // handleChange: (item) => this.saveRemarks(item)
        // },
        {
          module: 'input',
          prop: 'remarks',
          label: '不合格原因',
          align: 'center',
          minWidth: '200',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center',
          showOverflowTooltip: false,
          handleChange: (row) => {
            const params = {
              barCode: row.barCode,
              remarks: row.remarks
            }
            editUnqualifiedBarcodeReasonApi(params)
              .then(res => {
                console.log('res: ', res)
              })
              .catch(err => {
                console.log('err: ', err)
              })
          }
        }
      ],
      tableData: [],
      isStickyBtn: true
    }
  },
  computed: {
    isShowHistoryUnqualified() {
      return !!this.tableData.length && this.tableData.some(c => c.deliveryMaterialDetailId === this.currentItem.deliveryMaterialDetailId)
    }
  },
  watch: {
    'node.id': {
      handler: function(val) {
        if (val) {
          this.baseInfo = INIT_FORM()
          this.initInfo()
        }
      },
      immediate: true
    },
    visible: {
      handler(val) {
        val && (this.isStickyBtn = false)
        !val && (this.isStickyBtn = true)
      },
      immediate: true
    },
    visible1: {
      handler(val) {
        val && (this.isStickyBtn = false)
        !val && (this.isStickyBtn = true)
      },
      immediate: true
    }
  },
  mounted() {
    // this.packageCodeList.forEach(item => {
    //   this.getUnqualifiedBarcodeList({ deliveryMaterialDetailId: item.deliveryMaterialDetailId })
    //     .then(res => {
    //       const { deliveryMaterialDetailId } = this.item
    //       this.tableData = [...this.tableData, ...res.data]
    //       const count = this.tableData.reduce((total, cur) => {
    //         if (cur.deliveryMaterialDetailId === deliveryMaterialDetailId) {
    //           return total + cur.number
    //         }
    //         return 0
    //       }, 0)
    //       console.log('count---', count)
    //       item.number = item.num - count
    //     })
    // })
  },
  methods: {
    // 物料信息(详情)
    initInfo() {
      const params = {
        deliveryMaterialId: this.node.id || '' // 交接物料id
      }
      return new Promise((resolve, reject) => {
        getDetailApi({
          ...params
        }).then(res => {
          const { code, data } = res
          if (!code) {
            const { inspectType, ...otherParams } = data
            this.baseInfo = otherParams
            this.form = { inspectType } // 赋值检验类型
            // 不按最小单位SKU打码的展示包装条码列表
            !this.baseInfo.useMinUnit && this.getPackageCodeList()

            this.tableData = data.dmiList.filter(c => c.isShow)
          }
          resolve(res)
        }).catch(err => {
          reject(err)
        })
      })
    },
    // 包装条码(列表)
    getPackageCodeList() {
      this.dmdLoading = true
      return new Promise((resolve, reject) => {
        packageCodeSamplingListApi({
          deliveryMaterialId: this.node.id
        }).then(res => {
          const { code, data } = res
          if (!code) {
            this.packageCodeList = data.map(c => ({ ...c, num: c.number }))
          }
          this.dmdLoading = false
          resolve(res)
        }).catch(err => {
          this.dmdLoading = false
          reject(err)
        })
      })
    },
    // 包装条码(列表)-->点击
    handleRowClick1({ row, rowIndex, column, cell, event }) {
      if (column.label === '操作') {
        return false
      }
    },
    // 不合格品条码(列表)-->点击
    handleRowClick2({ row, rowIndex, column, cell, event }) {
      if (column.label === '操作') {
        return false
      }
    },
    // 不合格品打码(弹窗)-->打开弹窗
    handleCaptchaHumanBypass(row) {
      this.visible = true
      this.currentItem = row
    },
    // 不合格品扫码(弹窗)-->打开弹窗
    handleScanQRCodes() {
      this.visible1 = true
    },
    // 不合格品条码(列表)-->打印条码
    handlePrintBarcode(item) {
      this.getDataAndPrint(barcodePrintApi, {
        ...item
      })
    },
    // 不合格品条码(列表)-->删除
    handleDeleteBarcode(item, ind) {
      this.isStickyBtn = false
      this.$confirm('此操作将永久删除选中信息, 是否继续?', '提示', {
        confirmButtonText: '确 定',
        cancelButtonText: '取 消',
        type: 'warning',
        cancelButtonClass: 'btn-custom-cancel',
        lockScroll: false
      }).then(() => {
        deleteUnqualifiedBarcodeApi({ barCode: item.barCode })
          .then(res => {
            console.log('res: ', res)
            // 删除条码需同步包装条码列表里的数量
            const { deliveryMaterialDetailId, number } = item
            this.packageCodeList.forEach((item, index) => {
              if (item.deliveryMaterialDetailId === deliveryMaterialDetailId) {
                item.number = item.number + number
              }
            })
            // 执行删除
            this.tableData = this.tableData.filter(c => c.barCode !== item.barCode)
            this.$notify.success({
              title: '成功',
              message: '删除成功'
            })
          })
          .catch(err => {
            console.log('err: ', err)
          })
        this.isStickyBtn = true
      }).catch((e) => {
        console.log(e)
        this.$notify.info({
          title: '消息',
          message: '已取消删除'
        })
        this.isStickyBtn = true
      })
    },
    // 不合格品扫码(弹窗)-->确认
    handleScanConfirm(list = []) {
      list.forEach(c => {
        this.tableData.push(c)
      })
    },
    // 不合格品打码(弹窗)-->确认
    handleCaptchaHumanBypassConfirm(list = []) {
      const { deliveryMaterialDetailId } = this.currentItem
      if (!list.length) return
      // 清空原有打码记录
      this.tableData = this.tableData.filter(c => c.deliveryMaterialDetailId !== list[0].deliveryMaterialDetailId)
      // 追加新的打码记录
      list.forEach(c => {
        this.tableData.push(c)
      })
      const count = this.tableData.reduce((total, cur) => {
        if (cur.deliveryMaterialDetailId === deliveryMaterialDetailId) {
          return total + cur.number
        }
        return 0
      }, 0)
      console.log('count---', count)
      this.packageCodeList.forEach(c => {
        if (c.deliveryMaterialDetailId === deliveryMaterialDetailId) {
          c.number = c.num - count
        }
      })
    },
    // 查询已生成条码号
    getUnqualifiedBarcodeList(params = {}) {
      return new Promise((resolve, reject) => {
        getUnqualifiedBarcodeListApi({
          ...params
        }).then(res => {
          const { code, data } = res
          if (!code) {
            console.log('data: ', data)
          }
          resolve(res)
        }).catch(err => {
          reject(err)
        })
      })
    },
    // 保存
    handleSave() {
      // if (!this.tableData.length) {
      //   this.$notify.warning({
      //     title: '警告',
      //     message: '条码不能为空！'
      //   })
      //   return
      // }
      this.$refs['form'].validate((valid) => {
        if (!valid) return
        this.isStickyBtn = false
        this.$confirm('是否保存检验结论?', '提示', {
          confirmButtonText: '确 定',
          cancelButtonText: '取 消',
          type: 'warning',
          cancelButtonClass: 'btn-custom-cancel',
          lockScroll: false
        })
          .then(() => {
            // 确认后进入下一物料详情
            const extralist = this.baseInfo.dmiList.filter(item => !item.isShow)
            saveApi({
              deliveryMaterialId: this.node.id || '', // 交接物料ID
              // tip 抽检的时候需把没被抽检的一并传给后端，没被抽检的就是isShow为false的条码
              dmiList: [...this.tableData, ...extralist], // 条码列表
              packageCodeSamplingList: this.packageCodeList, // 包装码列表
              deliveryReceiptId: this.$route.params.id, // 交接单id
              ...this.form
            })
              .then(res => {
                const { code } = res
                if (!code) {
                  this.$notify.success({
                    title: '成功',
                    message: '保存成功'
                  })
                  this.$emit('enterNextMaterial')
                }
              })
              .catch(err => {
                console.log('---err---', err)
              })
            this.isStickyBtn = true
          })
          .catch((e) => {
            console.log(e)
            this.$notify.info({
              title: '消息',
              message: '已取消保存'
            })
            this.isStickyBtn = true
          })
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.app-container {
  width: 100%;
  height: 100%;
  overflow-y: auto;
  scrollbar-width: thin;
  scrollbar-color: #888 #f1f1f1; // 设置滚动条滑块跟轨道颜色
  overscroll-behavior: contain;
  -ms-scroll-chaining: contain;
  position: relative;
}
.sticky-header {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  // background-color: #FFF;
  text-align: right;
  z-index: 9999;
}
.absolute-header {
  position: -webkit-absolute;
  position: absolute;
  top: 0;
  right: 0;
  // background-color: #FFF;
  text-align: right;
  // z-index: 9999;
}
.save-btn {
  margin-right: 15px;
}
// 上半部分
::v-deep .box-card-rt {
  // border-width: 0 0 1px 0;
  .el-card__header {
    // padding: 18px 20px 0;
    // padding: 0;
    padding: 18px 0 0;
    border: none;
  }
}
// 下半部分
::v-deep .box-card-rb {
  border-width: 0 0 0 0;
  .el-card__header {
    // padding: 18px 20px 0;
    padding: 18px 0 0;
    border: none;
  }
}
</style>

