<template>
  <el-dialog
    :title="title"
    top="15vh"
    :visible="value"
    :show-close="true"
    width="750px"
    :lock-scroll="false"
    :destroy-on-close="true"
    :append-to-body="true"
    :close-on-click-modal="false"
    :close-on-press-escape="false"
    :before-close="beforeClose"
    @open="initSelect"
  >
    <div class="clearfix top-filter">
      <el-form ref="filterItem" :model="query" inline @submit.native.prevent>
        <el-form-item prop="splitNum" label="条码数量：">
          <el-input-number v-model="query.splitNum" :max="barCodeNumber" :min="1" style="width: 178px;" class="filter-item" placeholder="请输入条码数量" />
        </el-form-item>
        <el-button
          class="filter-item primary-btn"
          type="primary"
          @click="handleGenerateBarcode"
        >打码</el-button>
      </el-form>
    </div>
    <el-scrollbar class="scroll-dialog">
      <table-com
        ref="baseTableRef"
        :loading="loading"
        :index="true"
        :selection="false"
        :table-height-type="430"
        :table-column-sort="false"
        :column-list="columnList"
        :table-data="tableData"
        :operation-width="100"
        :show-operate="true"
      >
        <template slot-scope="scope">
          <el-button type="danger" :disabled="!scope.row.isCanEdit" @click="handleDelete(scope.row)">删除</el-button>
        </template>
      </table-com>
    </el-scrollbar>
    <div slot="footer">
      <el-button type="primary" @click="handleConfirm">确 定</el-button>
      <el-button @click="beforeClose">取 消</el-button>
    </div>
  </el-dialog>
</template>

<script>
import paginationPage from '@/mixins/paginationPage'
import { getGenerateBarcodeListApi, getUnqualifiedBarcodeListApi, saveUnqualifiedBarcodeListApi } from '@/apis/modules/in-storage/inspection'

export default {
  components: {
  },
  mixins: [paginationPage],
  props: {
    value: {
      type: Boolean,
      default: false
    },
    extraInfo: {
      type: Object,
      default: () => ({})
    },
    title: {
      type: [String, Number],
      default: '不合格品打码'
    },
    // 条码总数量
    barCodeNumber: {
      type: [String, Number],
      default: ''
    }
    // 父级页面不合格条码列表(传参目的：保证父级列表每条数据的唯一性)
    // barCodeList: {
    //   type: Array,
    //   default: () => ([])
    // }
  },
  data() {
    return {
      loading: false,
      query: {
        splitNum: 1
      },
      columnList: [
        {
          module: 'word',
          prop: 'barCode',
          label: '条码号',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        // {
        //   module: 'word',
        //   prop: 'snNo',
        //   label: '序列号',
        //   align: 'center',
        //   fixed: false,
        //   sortable: false,
        //   resizable: true,
        //   formatter: null,
        //   headerAlign: 'center'
        // },
        {
          module: 'number',
          prop: 'number',
          label: '数量',
          align: 'center',
          width: 160,
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center',
          setMin: (row) => 1,
          disabled: (row) => !row.isCanEdit,
          setMax: (row) => {
            const totalNumber = this.tableData.reduce((total, cur) => total + cur.number, 0)
            if (totalNumber === this.barCodeNumber) {
              return row.number
            }
          },
          handleChange: (row) => {
            // const totalNumber = this.tableData.reduce((total, cur) => total + cur.number, 0)
            // if (totalNumber > this.barCodeNumber) {
            //   this.$notify.warning({
            //     title: '警告',
            //     message: `打码数量(${totalNumber})超出总数量(${this.barCodeNumber})，请重新打码！`
            //   })
            // }
          },
          showOverflowTooltip: false
        },
        {
          module: 'word',
          prop: 'unitName',
          label: '计量单位',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'input',
          prop: 'remarks',
          label: '不合格原因',
          align: 'center',
          width: 160,
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
          // disabled: (row) => !row.isCanEdit
        }
      ],
      tableData: [],
      scanDisabled: false
    }
  },
  methods: {
    initSelect() {
      this.query.splitNum = 1
      this.getUnqualifiedBarcodeList()
    },
    // 查询已生成条码号
    getUnqualifiedBarcodeList() {
      const params = { deliveryMaterialDetailId: this.extraInfo.deliveryMaterialDetailId, isShowHistoryUnqualified: this.extraInfo.isShowHistoryUnqualified }
      return new Promise((resolve, reject) => {
        getUnqualifiedBarcodeListApi({
          ...params
        }).then(res => {
          const { code, data } = res
          if (!code) {
            this.tableData = data
          }
          resolve(res)
        }).catch(err => {
          reject(err)
        })
      })
    },
    handleGenerateBarcode() {
      const totalNumber = this.tableData.reduce((total, cur) => total + cur.number, 0)
      if (totalNumber === this.barCodeNumber) {
        this.$notify.warning({
          title: '警告',
          message: `打码数量已达最大拆出限度，不可再拆！`
        })
        return
      }
      const params = { ...this.extraInfo, ...this.query }
      return new Promise((resolve, reject) => {
        getGenerateBarcodeListApi({
          ...params
        }).then(res => {
          const { code, data } = res
          if (!code) {
            this.tableData = data.map(c => ({ ...c, number: c.isCanEdit ? 1 : c.number }))
          }
          resolve(res)
        }).catch(err => {
          reject(err)
        })
      })
    },
    handleDelete(item) {
      this.$confirm(`此操作将永久删除该条码, 是否继续?`, '提示', {
        confirmButtonText: '确 定',
        cancelButtonText: '取 消',
        cancelButtonClass: 'btn-custom-cancel',
        type: 'warning',
        lockScroll: false
      }).then(() => {
        this.tableData = this.tableData.filter(c => c.barCode !== item.barCode)
        this.$notify.success({
          title: '成功',
          message: `该不合格品已删除`
        })
      }).catch(() => {
        this.$notify.info({
          title: '消息',
          message: '已取消删除'
        })
      })
    },
    // 保存不合格品打码结果
    handleConfirm() {
      const totalNumber = this.tableData.reduce((total, cur) => total + cur.number, 0)
      if (totalNumber > this.barCodeNumber) {
        this.$notify.warning({
          title: '警告',
          message: `打码数量(${totalNumber})超出总数量(${this.barCodeNumber})，请重新打码！`
        })
        return
      }
      // const arr = this.barCodeList.map(c => c.barCode)
      // const realResult = this.tableData.filter(c => !arr.includes(c.barCode))
      const params = {
        deliveryMaterialDetailId: this.extraInfo.deliveryMaterialDetailId,
        unqualifiedBarcodeList: this.tableData
      }
      saveUnqualifiedBarcodeListApi(params)
        .then(res => {
          console.log('res: ', res)
          this.$emit('confirm', this.tableData)
          this.$emit('input', false)
        })
        .catch(err => {
          console.log('err: ', err)
          this.$emit('input', false)
        })
    },
    beforeClose(done) {
      typeof done === 'function' && done()
      this.$emit('input', false)
    }
  }
}
</script>

<style lang="scss" scoped>
.scroll-dialog {
    height: 440px;
}
::v-deep .el-scrollbar .el-scrollbar__wrap {
    overflow-x: hidden;
}
::v-deep .el-dialog__body {
    padding-bottom: 0;
}
::v-deep .top-filter .primary-btn {
    float: none !important;
    margin-left: 0 !important;
}
</style>

