<template>
  <div class="tabs-wrapper" :style="{height}">
    <div class="tabs-container">
      <div ref="tabsLeft" class="tabs-left">
        <div
          v-for="(item, index) in data"
          :id="index === currentIndex ? 'isActive' : 'tabItem'"
          :key="index"
          class="label"
          :class="{'is-active': index === currentIndex}"
          @click="handleClick(item, index)"
        >
          <!-- :style="{width: index === currentIndex ? '200px' : '180px'}" -->
          <div class="serialNo">{{ index + 1 }}</div>
          <div class="tag">
            <div class="code">{{ item.materialCode }}</div>
            <div class="name">{{ item.materialName }}</div>
          </div>
        </div>
      </div>
      <div id="tabsRight" class="tabs-right">
        <slot name="main-container" :handleClick="handleClick" />
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    height: {
      type: [String, Number],
      default: '550px'
    },
    data: {
      type: Array,
      default: () => ([])
    }
  },
  data() {
    return {
      currentIndex: 0
    }
  },
  mounted() {
    // 默认定位到第一个待检验的物料
    const info = this.data.find(c => [1].includes(c.status))
    if (info) {
      this.data.forEach((c, i) => {
        if (info.id === c.id) {
          this.handleClick(info, i)
        }
      })
      return
    }
    this.handleClick(this.data[0], 0)
  },
  methods: {
    handleClick(item, index) {
      this.currentIndex = index
      this.$emit('tabPanelClick', { item, index })
      this.$nextTick(() => {
        const childElement = document.getElementById('isActive')
        const parentElement = childElement.parentNode
        parentElement.scrollTo({ top: childElement.offsetTop, behavior: 'smooth' })
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.tabs-wrapper {
    border: 1px solid #E4E7ED;
    background-color: #FFF;
    padding: 20px;
    box-sizing: border-box;
    .tabs-container {
        height: 100%;
        display: flex;
        justify-content: space-between;
        .tabs-left {
            width: 200px;
            min-height: 100%;
            overflow-y: auto;
            scrollbar-width: none;
            position: relative;
            .label {
                width: 180px;
                height: 45px;
                border-radius: 5px;
                padding: 5px;
                box-sizing: border-box;
                border: 1px solid #E4E7ED;
                background-color: #FFF;
                margin-bottom: 15px;
                font-size: 12px;
                display: flex;
                justify-content: space-between;
                align-items: center;
                .serialNo {
                  width: 25px;
                }
                .tag {
                  width: calc(100% - 25px);
                  .code {
                      font-weight: bold;
                  }
                  .name {
                      white-space: nowrap;
                      overflow: hidden;
                      text-overflow: ellipsis;
                  }
                }
            }
            .is-active {
                border-top-color: #409EFF;
                border-right-color: #409EFF;
                border-bottom-color: #409EFF;
                border-left-color: #409EFF;
                border-width: 2px;
                box-shadow: 0 0 15px 2px rgba(0, 0, 0, 0.7)
            }
        }
        .tabs-right {
            width: calc(100% - 200px);
            min-height: 100%;
            border: 2px solid #409EFF;
            padding: 15px;
            box-sizing: border-box;
        }
    }
}
</style>
