<template>
  <el-dialog
    :title="title"
    top="15vh"
    :visible="value"
    :show-close="true"
    width="750px"
    :lock-scroll="false"
    :destroy-on-close="true"
    :append-to-body="true"
    :close-on-click-modal="false"
    :close-on-press-escape="false"
    :before-close="beforeClose"
    @open="initSelect"
  >
    <div class="clearfix top-filter">
      <el-form ref="form" :model="query" inline @submit.native.prevent>
        <el-form-item prop="barCode" label="条码号：">
          <el-input v-model="query.barCode" clearable placeholder="请输入条码号" style="width: 178px;" class="filter-item" @keyup.enter.native="search" />
        </el-form-item>
        <el-button
          class="filter-item"
          type="success"
          icon="el-icon-search"
          @click="search"
        >搜索</el-button>
        <el-button
          class="filter-item"
          type="warning"
          icon="el-icon-refresh-left"
          @click="resetForm('form')"
        >重置</el-button>
      </el-form>
    </div>
    <el-scrollbar class="scroll-dialog">
      <table-com
        ref="baseTableRef"
        :loading="loading"
        :index="true"
        :selection="false"
        :table-height-type="430"
        :table-column-sort="false"
        :column-list="columnList"
        :table-data="tableData"
        :operation-width="100"
        :show-operate="true"
      >
        <template slot-scope="scope">
          <el-button type="danger" @click="handleDelete(scope.row)">删除</el-button>
        </template>
      </table-com>
    </el-scrollbar>
    <div slot="footer">
      <el-button type="primary" @click="handleConfirm">确 定</el-button>
      <el-button @click="beforeClose">取 消</el-button>
    </div>
  </el-dialog>
</template>

<script>
import paginationPage from '@/mixins/paginationPage'
import scan from '@/mixins/scan.js'
import { getUnqualifiedBarcodeScanListApi } from '@/apis/modules/in-storage/inspection'
const INIT_FORM = () => ({
  barCode: ''
})

export default {
  components: {
  },
  mixins: [paginationPage, scan],
  props: {
    value: {
      type: Boolean,
      default: false
    },
    extraInfo: {
      type: Object,
      default: () => ({})
    },
    title: {
      type: [String, Number],
      default: '不合格品扫码'
    },
    // 父级页面不合格条码列表(传参目的：保证父级列表每条数据的唯一性)
    barCodeList: {
      type: Array,
      default: () => ([])
    }
  },
  data() {
    return {
      loading: false,
      query: INIT_FORM(),
      columnList: [
        {
          module: 'word',
          prop: 'barCode',
          label: '条码号',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'snNo',
          label: '序列号',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'number',
          label: '数量',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'select',
          prop: 'inspectResult',
          label: '检验结论',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center',
          placeholder: '请选择',
          optionLabel: 'name', // 下拉选框绑定的 label 字段的key
          optionValue: 'id', // 下拉选框绑定的 value 字段的key
          setDisabled: (row) => {
            return true
          },
          getOptions: (item) => {
            return [{ name: '不合格', id: false }, { name: '合格', id: true }]
          }
        }
      ],
      tableData: [],
      scanDisabled: false
    }
  },
  methods: {
    initSelect() {
      this.resetForm('form')
      this.tableData = []
    },
    // 扫码或输入框填写内容回车动作下执行的公共逻辑
    handlePublicFn(obj) {
      const arr = obj.barCode.split(':')
      const barCode = arr.length > 1 ? arr[1] : arr[0]

      // 扫码结果跟外部的某个不合格条码重复
      const codes = this.barCodeList.map(c => c.barCode)
      const isRepeat = codes.includes(barCode)
      if (isRepeat) {
        this.$notify.warning({
          title: '警告',
          message: `条码号${barCode}已存在，请重新扫码！`
        })
        return
      }
      // 查询条件赋值
      this.query.barCode = barCode
      // 扫码结果去重
      const exist = this.tableData.some(item => item.barCode.indexOf(barCode) !== -1)
      if (!exist) {
        const params = { ...this.extraInfo, ...this.query }
        return new Promise((resolve, reject) => {
          getUnqualifiedBarcodeScanListApi({ // 不合格品扫码
            ...params
          }).then(res => {
            const { code, data } = res
            if (!code) {
              // 搜索或扫码后调接口会生成一条不合格条码信息，将最新生成信息放到首位
              if (data && Array.isArray(data) && !!data.length) {
                this.tableData = [...data, ...this.tableData]
              }
            }
            resolve(res)
          }).catch(err => {
            reject(err)
          })
        })
      }
    },
    scanEnd(data) {
      const obj = JSON.parse(data)
      this.handlePublicFn(obj)
    },
    search() {
      if (!this.query.barCode) return
      this.handlePublicFn(this.query)
    },
    // 弹窗关闭前操作
    beforeClose(done) {
      typeof done === 'function' && done()
      this.$emit('input', false)
    },
    handleDelete(item) {
      this.$confirm(`此操作将永久删除该条码, 是否继续?`, '提示', {
        confirmButtonText: '确 定',
        cancelButtonText: '取 消',
        cancelButtonClass: 'btn-custom-cancel',
        type: 'warning',
        lockScroll: false
      }).then(() => {
        this.tableData = this.tableData.filter(c => c.barCode !== item.barCode)
        this.$notify.success({
          title: '成功',
          message: `该不合格品已删除`
        })
      }).catch(() => {
        this.$notify.info({
          title: '消息',
          message: '已取消删除'
        })
      })
    },
    handleConfirm() {
      const arr = this.barCodeList.map(c => c.barCode)
      const realResult = this.tableData.filter(c => !arr.includes(c.barCode))
      this.$emit('confirm', realResult)
      this.$emit('input', false)
    },
    resetForm(formName) {
      this.$refs[formName].resetFields()
      this.query = INIT_FORM()
    }
  }
}
</script>

<style lang="scss" scoped>
.scroll-dialog {
    height: 440px;
}
::v-deep .el-scrollbar .el-scrollbar__wrap {
    overflow-x: hidden;
}
::v-deep .el-dialog__body {
    padding-bottom: 0;
}
</style>

